import React, { Component } from "react";
/*import { Link } from "react-router-dom";*/
import { Button, Container, Col, Row, Image } from "react-bootstrap";
import WOW from "wowjs";
import "./Body.css";
import twoMob from "../assets/twoMobiles.png";
import twoMob1 from "../assets/twoMobiles2.png";
import third from "../assets/pic3.png";

class Body extends Component {
  state = {
    head: [
      {
        Heading: "Simple steps away from fame",
        Text:
          "Through a few simple steps can make your shop up to the public quickly."
      },
      {
        bold: "Quick statistics",
        Heading: "and details of all the tasks you performed by cashier",
        Text:
          "Some pages in can find out what's new and special for your customer details and the work carried out through the application"
      }
    ],
    body: [
      {
        sapn: "You are the strongest in Google results",
        bold: "Easily ",
        Heading: "and super fast your name becomes the best in Google",
        Text: `here at NasNav we Designed a device called the route box
          that plugs into a stores barcode scanner. The box Picks up
          products UPC codes,and uses that to populate a website of
          the Products here at NasNav we Designed a device called the
          route box`,
        image: twoMob
      },
      {
        sapn: "You are the strongest in Google results",
        bold: "Easily ",
        Heading: "and super fast your name becomes the best in Google",
        Text: `here at NasNav we Designed a device called the route box
          that plugs into a stores barcode scanner. The box Picks up
          products UPC codes,and uses that to populate a website of
          the Products here at NasNav we Designed a device called the
          route box`,
        image: twoMob1
      },
      {
        sapn: "You are the strongest in Google results",
        bold: "Easily ",
        Heading: "and super fast your name becomes the best in Google",
        Text: `here at NasNav we Designed a device called the route box
        that plugs into a stores barcode scanner. The box Picks up
        products UPC codes,and uses that to populate a website of
        the Products here at NasNav we Designed a device called the
        route box`,
        image: twoMob
      },
      {
        sapn: "You are You are the strongest in Google results ",
        bold: "",
        Heading: "Loyalty programs are structured marketing strategies",
        Text: `in marketing generally and in retailing more specifically, 
        a loyalty card, rewards card, points card, advantage 
        card, or club card is a plastic or paper card, visually 
        similar to a credit card
        
        By presenting such a card, purchasers typically earn 
        future purchases.`,
        image: third
      }
    ]
  };


  componentDidMount() {
    const wow = new WOW.WOW();
    wow.init();
  }

  render() {
    const head = number => {
      const hh = this.state.head[number];
      return (
        <Row className="paddingwidth">
          <Col xs={12} md={12} className="text-center">
            <div>
              <h2>
                <b>{hh.bold} </b>
                {hh.Heading}
              </h2>
              <p>{hh.Text}</p>
            </div>
          </Col>
        </Row>
      );
    };

    const textcolbody = numb => {
      const bb = this.state.body[numb];
      return (
        <Col xs={12} md={6} className="padingtop wow fadeInUp" data-wow-duration="1s" data-wow-delay="0.25s">
          <div>
            <span>{bb.sapn}</span>
            <h2>
              <b>{bb.bold}</b>
              {bb.Heading}
            </h2>
            <p>{bb.Text}</p>
            <Button className="navButton coloredBack">Get Started</Button>
          </div>
        </Col>
      );
    };

    const imgcolbody = source => {
      return (
        <Col xs={12} md={6} className="disFirst">
          <Image src={source} fluid  className="animate"/>
        </Col>
      );
    };

    return (
      <React.Fragment>
        <section className="firstSection">
          <Container className="body">
            {head(0)}
            <Row className="FirstWave">
            {textcolbody(0)}
              {imgcolbody(this.state.body[0].image)}
            </Row>
            <Row className="secondWave">
              {imgcolbody(this.state.body[1].image)}
              {textcolbody(1)}
            </Row>
            {head(1)}
            <Row className="FirstWave">
              {textcolbody(2)}
              {imgcolbody(this.state.body[2].image)}
            </Row>
            <Row className="FirstWave">
              {textcolbody(3)}
              {imgcolbody(this.state.body[3].image)}
            </Row>
          </Container>
        </section>
      </React.Fragment>
    );
  }
}

export default Body;
