import React, { Component } from "react";
/*import { Link } from "react-router-dom";*/
import { Navbar, Nav, Button, Container, Col, Row ,Image} from "react-bootstrap";
import "./Header.css";

import logo from "../assets/logo.png";
import mobSlider from "../assets/mobileSlider.png";

class Header extends Component {
  render() {
    return (
      <React.Fragment>
        <div className="">
          <div className="image">
            <Container>
              <Navbar expand="lg">
                <Navbar.Brand href="#home" className="float-md-left">
                  <img src={logo} alt="logo" />
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                  <Nav className="mx-auto">
                    <Nav.Link href="#home">Navbox</Nav.Link>
                    <Nav.Link href="#link">NavStyle</Nav.Link>
                    <Nav.Link href="#link">Shopping</Nav.Link>
                    <Nav.Link href="#link">360 Editor</Nav.Link>
                    <Nav.Link href="#link">Dashboard</Nav.Link>
                  </Nav>
                  <Button className="navButton float-md-right">
                    Get Started
                  </Button>
                </Navbar.Collapse>
              </Navbar>
            </Container>

            <Container className="headertext">
              {/* Stack the columns on mobile by making one full-width and the other half-width */}
              <Row>
                <Col lg={12} xl={6}>
                  <h2>
                    <b>Choose</b> examples to show products to your customers
                  </h2>
                  <Image src={mobSlider}  fluid alt="mobile" className="hidden-lg-up"/>
                  <p>
                    Our services are not to be reckoned with upload Producs can
                    slipingin your home and see theyou an edge over the online
                    competitors.of big businesses data
                  </p>
                  <Button className="navButton">Get Started</Button>
                </Col>
                <Col xs={12} md={6} />
              </Row>
            </Container>
          </div>
        </div>
        <div id="container">
          <svg x="0px" y="0px" viewBox="0 0 3841 108.5" className="sv">
            <defs />

            <path
              className="pa"
              d="M3360.5,97.739c-242,0-480-48.375-480-48.375
                      S2647.5,0.5,2400.5,0.5s-480,48.375-480,48.375s-238,48.864-480,48.864s-480-48.375-480-48.375S727.5,0.5,480.5,0.5
                      S0.5,48.875,0.5,48.875V108h1920h1920V48.875C3840.5,48.875,3602.5,97.739,3360.5,97.739z"
            />
          </svg>

          {/*<svg
            class=""
            fill="#4747f4"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 595.3 135.5"
            preserveAspectRatio="none"
          >
            <path d="M0 135.5V5.6C53.4-6.3 113 2.2 164 17.9c22.4 6.9 43.8 15.1 64.9 23.7 26.9 11 53.3 22.5 80.9 32.7 49.2 18.2 104.8 32.2 160.9 28.3 43.7-3 83.7-16.6 124.6-27.3v60.2H0zM0 135.5h595.3" />
          </svg>*/}
        </div>
      </React.Fragment>
    );
  }
}

export default Header;
