import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEnvelopeOpen } from "@fortawesome/free-regular-svg-icons";
import { faFacebookF } from "@fortawesome/free-brands-svg-icons";

import { Button, Container, Col, Row, Image } from "react-bootstrap";
import "./Footer.css";
import logoFooter from "../assets/logofooter.png";

class Footer extends Component {
  state = {
    branches: [
      {
        Location: "London",
        Address: "",
        Link: "",
        Phone: "0052635450",
        Mail: "london@nasorg.co"
      },
      {
        Location: "Egypt",
        Address: "",
        Link: "",
        Phone: "0052635450",
        Mail: "egypt@nasorg.co"
      },
      {
        Location: "Kuwait",
        Address: "",
        Link: "",
        Phone: "0052635450",
        Mail: "kuwatt@nasorg.co"
      },
      {
        Location: "Nigeria",
        Address: "",
        Link: "",
        Phone: "0052635450",
        Mail: "nigeria@nasorg.co"
      },
      {
        Location: "Poland",
        Address: "",
        Link: "",
        Phone: "0052635450",
        Mail: "poland@nasorg.co"
      }
    ]
  };
  render() {
    return (
      <React.Fragment>
        <div>
          <Container />

          <Container>
            {/* Stack the columns on mobile by making one full-width and the other half-width */}
            <Row className="MapBack">
              <Col xs={12} lg={3}>
                <div className="join-fam">
                  <h4>Join our family now wherever you are</h4>
                  <p>You can join us easily and increase your profit</p>
                  <Button className="navButton coloredBack">Get Started</Button>
                </div>
              </Col>
            </Row>
          </Container>
        </div>

        <Container>
          <Row className="contacts">
            {this.state.branches.map(branche => (
              <Col key={branche.Location} className="contact">
                <h3>{branche.Location}</h3>
                <p className="address">
                  The United Kingdom. It's located 51.51
                </p>
                <p>
                  <a href={branche.Link} className="blue-txt">
                    Customer Service
                  </a>
                </p>
                <div className="feature">
                  <p>
                    <FontAwesomeIcon icon="mobile-alt" />{branche.Phone}
                  </p>
                </div>
                <div className="feature">
                  <p>
                    <FontAwesomeIcon icon={faEnvelopeOpen} />
                    {branche.Mail}
                  </p>
                </div>
              </Col>
            ))}
          </Row>
        </Container>

        <Container>
          <Row className="mb-5">
            <Col md={6} xs={12}>
              <Image src={logoFooter} />
              <FontAwesomeIcon icon={faFacebookF} className="iconFooter" />
            </Col>
            <Col md={6} xs={12}>
              <div className="copyWrite">
                <p> 2019 Nasorg.com All Rights Reserved </p>
              </div>
            </Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default Footer;
